/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .edgtf-content,
div.woocommerce {

    .edgtf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .edgtf-quantity-input,
        .edgtf-quantity-minus,
        .edgtf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: transparent;
            text-align: center;
            box-sizing: border-box;
        }

        .edgtf-quantity-input {
            position: relative;
            height: 60px;
            width: 54px;
            padding: 0;
            margin: 0 30px 0 0;
            font-size: 22px;
            font-family: $default-heading-font;
            line-height: 54px;
            outline: 0;
            border: none;
            border-right: 1px solid $default-border-color;
        }

        .edgtf-quantity-minus,
        .edgtf-quantity-plus {
            position: absolute;
            right: 0;
            width: 30px;
            height: 30px;
            line-height: 28px;
            border-left: 0;
            cursor: pointer;
            @include edgtfTransition(color .15s ease-in-out);
            font-size: 18px;

            &:hover {
                color: $first-main-color;
            }
        }

        .edgtf-quantity-plus {
            top: 0;
            border-bottom: 1px solid $default-border-color;
        }

        .edgtf-quantity-minus {
            bottom: 0;
        }
    }
}

.woocommerce-page .edgtf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        position: relative;
        width: 100%;
        margin: 0 0 14px;
        font-family: inherit;
        font-weight: inherit;
        color: $default-text-color;
        padding: 18px 20px;
        font-size: 15px;
        line-height: 20px;
        background-color: transparent;
        border: 1px solid $default-border-color;
        outline: 0;
        -webkit-appearance: none;
        box-sizing: border-box;

        &:focus {
            border-color: $default-heading-color;
        }
    
        @include edgtfPlaceholder {
            color: inherit;
        }
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */