/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.edgtf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    padding: 0 10px;
    margin: 0 13px;

    .touch & {
        cursor: pointer;
    }

    .edgtf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .edgtf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;

        .edgtf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
            line-height: 1em;
            width: 24px;
            height: 35px;
            background-image: url(../../assets/img/cart.png);
            background-repeat: no-repeat;
            text-align: center;

            .edgtf-cart-number{
                font-family: $default-heading-font;
                font-size: 13px;
                font-weight: 300;
                line-height: 35px;
            }
        }
    }

    &:hover {

        .edgtf-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.edgtf-dark-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-shopping-cart-holder .edgtf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.edgtf-light-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-shopping-cart-holder .edgtf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }

    .edgtf-cart-icon{
        background-image: url(../../assets/img/cart-white.png);
    }
}

.edgtf-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 292px;
    background-color: #fff;
    border-top: 1px solid #e0e4f2;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    @include edgtfTransition(all 0.35s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 20px;
        padding: 0;

        li {
            @include edgtfTableLayout();
            padding: 0 0 10px;
            box-sizing: border-box;

            &.edgtf-empty-cart {
                color: initial;
                border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .edgtf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 79px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .edgtf-item-info-holder {
        @include edgtfTableCellLayout();
        vertical-align: middle;
        padding-left: 20px;

        span{
            font-family: $default-heading-font;
            color: rgba($default-heading-color, .4);
            font-size: 16px;
            font-weight: 300;
            text-transform: none;
            letter-spacing: 0.02em;
            line-height: 1.63em;
            font-style: italic;
        }

        .edgtf-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
        }

        .remove {
            position: absolute;
            top: -1px;
            right: 0;
	        font-size: 22px;
	        line-height: 1;

            span {
                display: block;
                line-height: inherit;
                color: $default-heading-color;

                &:hover{
                    color: rgba($default-heading-color, 0.7);
                }

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .edgtf-quantity {
            display: inline-block;
            margin: 3px 0 0;
        }

        .amount {
            display: inline-block;
            margin: 3px 0 0;
        }
    }

    .edgtf-cart-bottom {
        padding: 21px 0 0;

        .edgtf-subtotal-holder {
            position: relative;
            display: block;
            width: 100%;
            margin: 0 0 13px;
            border-bottom: 1px solid $default-border-color;
            padding: 6px 0 11px;

            > * {
                display: inline-block;
                vertical-align: top;
                color: $default-heading-color;
                line-height: 20px;
                margin: 0;
            }

            .edgtf-total-amount {
                text-align: right;
	            font-size: 16px;
            }
        }

        .edgtf-my-cart{
            float: right;
        }

        .edgtf-checkout,
        .edgtf-my-cart{
            font-family: $default-heading-font;
            font-size: 16px;
            font-weight: 300;

            &:hover{
                color: rgba($default-heading-color, .4);
            }
        }
    }
}

.edgtf-vertical-menu-area {

    .edgtf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */