/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .edgtf-content,
div.woocommerce {
    
    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.edgtf-woo-search-widget-button),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include edgtfButtonDefaultStyle();
        @include edgtfButtonMediumParams();
        @include edgtfButtonSolidColor();
        white-space: nowrap;
        cursor: pointer;
        z-index: 3;
        border: none;
        background-image: url(../../assets/img/default_pattern.png);
        background-color: $default-background-color;
        font-weight: 400;
        color: $default-heading-color;

        &:hover {
            background: $default-heading-color;
            color: $default-background-color;
        }
    }

    a.button{
        background-color: transparent;
        background-image: none;
        color: #fff;
        padding: 0 10px 0;

        &:hover{
            background-color: transparent;
            color: rgba(255,255,255, .8);
        }

        &:after{
            content: url(../../assets/img/hover-cart.png);
            right: -10px;
            position: relative;
            bottom: -3px;
        }

        &.added{
            display: none;
        }
    }
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */